/* Î©îÏù∏ ÌéòÏù¥ÏßÄÏóêÏÑú ÏÉÅÌíà Ïù¥ÎØ∏ÏßÄÎ•º ÌÅ¥Î¶≠ÌñàÏùÑ Îïå ÎÇòÏò§Îäî ÏÉÅÏÑ∏ÌéòÏù¥ÏßÄ */

import React, { useState, useEffect, useContext } from 'react';
import { useHistory, useParams } from 'react-router-dom';
import { Nav } from "react-bootstrap";
import { CSSTransition } from 'react-transition-group';
import { connect } from 'react-redux';
import { useStockContext } from '../App.js';
import './Detail.scss';


function Detail(props){

  // useEffectÎ•º ÌôúÏö©ÌïòÏó¨ mount ÎÅùÎÇ¨ÏùÑ Îïå, Ïû¨Í≥† ÏïåÎ¶ºÏ∞Ω ÎùÑÏö∞Í∏∞
  let [alertTop, setAlertTop] = useState(true);
  useEffect(() => {
    let timer = setTimeout(() => {
      setAlertTop(false);
    }, 2000);
    return () => { clearTimeout(timer) } /* return+Ìï®Ïàò: Ïª¥Ìè¨ÎÑåÌä∏Í∞Ä ÏÇ¨ÎùºÏßà Îïå ÌÉÄÏù¥Î®∏Î•º ÏóÜÏï†Îäî ÏΩîÎìú Ï∂îÍ∞Ä */
  }, [alertTop]); /* [alertTop]: Detail Ïª¥Ìè¨ÎÑåÌä∏Í∞Ä Î°úÎìúÎê† Îïå+alertTopÎùºÎäî stateÍ∞Ä Î≥ÄÍ≤ΩÏù¥ Îê† ÎïåÎßå useEffect()Í∞Ä Ïã§ÌñâÎêòÎèÑÎ°ù Ìï®*/


  let { id } = useParams(); /* useParams()=ÌòÑÏû¨ URLÏóê Ï†ÅÌûå Î™®Îì† ÌååÎùºÎØ∏ÌÑ∞Î•º objectÌòïÏãùÏúºÎ°ú Ï†ÄÏû•Ìï¥Ï£ºÎäî Ìï®Ïàò */
  let Ï∞æÏùÄÏÉÅÌíà = props.shoes.find(function(ÏÉÅÌíà){
    return ÏÉÅÌíà.id == id
  });
  // find() ÌôúÏö©: ÌòÑÏû¨ URLÏùò /:idÏóê Ï†ÅÌûå Í∞íÍ≥º ÏÉÅÌíàÏùò ÏòÅÍµ¨Î≤àÌò∏(ÏÉÅÌíà.id)Í∞Ä Í∞ôÏùÄÏßÄ ÎπÑÍµê, Ï∞∏Ïù¥Î©¥ Î≥ÄÏàòÏóê Ï†ÄÏû•Ìï® -> Ï∞æÏùÄ ÏÉÅÌíàÏù¥ÎùºÎäî Î≥ÄÏàòÎ•º Ïù¥Ïö©Ìï¥ Îç∞Ïù¥ÌÑ∞Î∞îÏù∏Îî©


  let [tab, setTab] = useState(0);
  let [tabIn, setTabIn] = useState(false); 
  let history = useHistory(); /* Îí§Î°úÍ∞ÄÍ∏∞ Î≤ÑÌäºÏùÑ ÏúÑÌïú useHistory Hook */
  let [size, setSize] = useState(''); /* size value Ï†ÄÏû• */
  

  // Detail Ïª¥Ìè¨ÎÑåÌä∏ Î°úÎìúÏãú Ìà¨Î™ÖÎèÑÍ∞Ä 0ÏóêÏÑú 1Î°ú ÏÑúÏÑúÌûà Ï¶ùÍ∞ÄÌïòÎäî Ïï†ÎãàÎ©îÏù¥ÏÖò Ìö®Í≥º
  let [fade2, setFade2] = useState('');
  useEffect(() => {
    setFade2("end"); /* Detail.scssÎÇ¥Î∂ÄÏùò ÌÅ¥ÎûòÏä§Î•º Ï∂îÍ∞ÄÌïòÏó¨ Ìö®Í≥º Ï†ÅÏö© */
    return () => {
      setFade2('');
    }
  }, []);

  // 'stock' state Context API ÏÇ¨Ïö©(App.js)
  let useStock = useContext(useStockContext);

  return(  
    <div className={`container detail start ${fade2}`}> {/* Ïï†ÎãàÎ©îÏù¥ÏÖò Ï†ÅÏö© */}

      {/* ÏïåÎ¶ºÏ∞Ω */}
      { alertTop === true
        ? (<div className="my-alert"><p>Ïû¨Í≥†Í∞Ä ÏñºÎßà ÎÇ®ÏßÄ ÏïäÏïòÏäµÎãàÎã§!</p></div>)
        : null
      }

      {/* ÏÉÅÌíà card */}
      <div className="row">
        <div className="col-md-4">
          <img src={`https://dygreen.github.io/React-study/img/shoes${Ï∞æÏùÄÏÉÅÌíà.id + 1}.jpg`} width="100%" />
        </div>
        <div className="col-md-6 mt-4">
          <h4 className="pt-5">{Ï∞æÏùÄÏÉÅÌíà.title}</h4>
          <p>{Ï∞æÏùÄÏÉÅÌíà.content}</p>
          <p>{Ï∞æÏùÄÏÉÅÌíà.price}Ïõê</p>


          {/* Ïû¨Í≥† */}
          <Info Ï∞æÏùÄÏÉÅÌíà={Ï∞æÏùÄÏÉÅÌíà} />


          {/* ÏÇ¨Ïù¥Ï¶à select box */}
          <select name="size" class="select_box" onChange={(e) => {
            let sizeValue = e.target.value;
            setSize(sizeValue);
          }}>
            <option>=== size ===</option>
            <option value="230">230</option>
            <option value="240">240</option>
            <option value="250">250</option>
            <option value="260">260</option>
            <option value="270">270</option>
          </select>


          {/* buttons */}
          <button className="btn btn-outline-dark" onClick={() => {
            // ÏÇ¨Ïù¥Ï¶à ÏÑ†ÌÉùÏùÑ ÏïàÌïòÎ©¥ ÏïåÎ¶ºÏ∞Ω, ÌïòÎ©¥ Ïû•Î∞îÍµ¨ÎãàÎ°ú Ï†ïÎ≥¥Ï†ÑÎã¨
            size == ''
            ? alert("ÏÇ¨Ïù¥Ï¶àÎ•º ÏÑ†ÌÉùÌï¥Ï£ºÏÑ∏Ïöî!")
            : orderInfo();

            function orderInfo(){
              // Ïû¨Í≥†Í∞Ä 0Î≥¥Îã§ ÌÅ¨Î©¥ -1, 0Ïù¥Î©¥ Í∞êÏÜå Ï§ëÎã®
              if(useStock[Ï∞æÏùÄÏÉÅÌíà.id] > 0){
                
                useStock[Ï∞æÏùÄÏÉÅÌíà.id]--; /* Ïû¨Í≥† -1 */
                props.dispatch({ /* ÏÉÅÌíàÏ†ïÎ≥¥ dispatch */
                  type:'add', 
                  data: {
                    id: Ï∞æÏùÄÏÉÅÌíà.id, 
                    name: Ï∞æÏùÄÏÉÅÌíà.title, 
                    quan: 1, 
                    size: size
                  }
                }); 
                history.push('/cart'); /* Cart pageÎ°ú Ïù¥Îèô */
        
              } else if (useStock[Ï∞æÏùÄÏÉÅÌíà.id] === 0){
                alert("Ïû¨Í≥†Í∞Ä ÏóÜÏäµÎãàÎã§üò¢ Îã§Î•∏ ÏÉÅÌíàÏùÄ Ïñ¥Îñ†ÏÑ∏Ïöî?");
              }
            }
          
            }}>Ï£ºÎ¨∏ÌïòÍ∏∞</button>
          <button onClick={() => { history.push('/cart')}} className='btn btn-outline-dark' style={{margin: '0 5px'}}>Ïû•Î∞îÍµ¨Îãà</button>
          <button onClick={() => { history.push('/') }} className="btn btn-dark">Îí§Î°úÍ∞ÄÍ∏∞</button>
        </div>
      </div>


      {/* tab */}
      <Nav variant="tabs" defaultActiveKey="link-0" className="tabs">
        <Nav.Item>
          <Nav.Link eventKey="link-0" onClick={() => {setTab(0)}}>Info</Nav.Link>
        </Nav.Item>
        <Nav.Item>
          <Nav.Link eventKey="link-1" onClick={() => {setTab(1)}}>Shipping</Nav.Link>
        </Nav.Item>
        <Nav.Item>
          <Nav.Link eventKey="link-2" onClick={() => {setTab(2)}}>Refund</Nav.Link>
        </Nav.Item>
      </Nav>
      <CSSTransition in={tabIn} classNames="tab" timeout={500}>
        <TabContent tab={tab} setTabIn={setTabIn}/>
      </CSSTransition>
    </div> 
  );
}

// Ïû¨Í≥† Îç∞Ïù¥ÌÑ∞ ÌëúÏãú Info Ïª¥Ìè¨ÎÑåÌä∏
function Info(props){
  let useStock = useContext(useStockContext);
  return (
    <p>Ïû¨Í≥†: {useStock[props.Ï∞æÏùÄÏÉÅÌíà.id]}</p>
  )
}

// ÌÉ≠(tab) Í∏∞Îä•
function TabContent({tab}){ /* {tab} => props */
  let [fade, setFade] = useState('');
  
  useEffect(() => {
    let tabTimer = setTimeout(() => {setFade("end")},100); /* Î¶¨Ïï°Ìä∏Ïùò automatic batching Í∏∞Îä•ÏùÑ Í≥†Î†§Ìïú setTimeout ÏΩîÎìú */
    return () => {
      clearTimeout(tabTimer);
      setFade('');
    }
  }, [tab]);

  return (<div className={`start ${fade} info`}>
    {[<div>ÏÉÅÌíàÏ†ïÎ≥¥ ÎÇ¥Ïö©ÏûÖÎãàÎã§</div>, <div>Î∞∞ÏÜ°Í¥ÄÎ†® ÎÇ¥Ïö©ÏûÖÎãàÎã§</div>, <div>ÌôòÎ∂àÏïΩÍ¥Ä ÎÇ¥Ïö©ÏûÖÎãàÎã§</div>][tab]}
  </div>)
}

function makeProps(state){
  return {
    state : state.reducer,
    alertTop : state.reducer2
  }
}

// memo()Î•º ÌÜµÌï¥ Î≥ÄÍ≤ΩÏù¥ Ïïà Îêú Ïª¥Ìè¨ÎÑåÌä∏Ïùò Ïû¨Î†åÎçîÎßÅÏùÑ Î∞©ÏßÄ
export default React.memo(connect(makeProps)(Detail));